<template>
    <div class="news-item" @click="setSelected" v-if="newsItem !== null" :class="{ notSelected: !selected, selected: selected }">
        <div v-show="!selected" class="holder">
            <div class="cover-image" :style="backgroundImage"></div>
            <div class="title-holder">
                <h3 class="title">{{ newsItem !== null ? newsItem.title : '' }}</h3>
                <div class="date">{{ newsItem !== null ? newsItem.date : '' }}</div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  props: {
    newsItem: {
      type: Object,
      default: function () {
        return {
          'id': 0,
          'title': 'test',
          'summary': '',
          'photos': [],
          'coverPhoto': ''
        }
      }
    },
    selected: {
      type: Boolean
    }
  },
  computed: {
    backgroundImage: function () {
      return 'background-image: url("assets/' + this.newsItem.folder + '/' + this.newsItem.coverPhoto + '");'
    }
  },
  methods: {
    setSelected: function () {
      this.$store.commit('setSelected', { item: this.newsItem })
    }
  }
}
</script>

<style lang="scss">
    @import '../styles/theme.scss';

    .news-item {
        border: 1px solid $secondaryColour;
        margin-right: 10px;
        margin-top: 10px;

        &.notSelected {
            .holder {
                display: flex;
                flex-direction: row;
                height: 100px;
                padding: 25px;
                cursor: pointer;
            }

            &:hover {
                -webkit-box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
                -moz-box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
                box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.75);
            }
        }

        &.selected {
            width: 100%;
            height: auto;
        }

        .cover-image {
            height: 100px;
            width: 100px;
        }

        .title-holder {
            padding: 10px;
            padding-left: 15px;
            text-align: left;

            .title {
                font-size: 1.3em;
                font-weight: bold;
            }

            .date {
                font-size: 0.8em;
                font-weight: italic;
                color: lightgray;
                position: relative;
                top: -$lineHeight;
            }
        }
    }
</style>



